<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<style lang="stylus" src="@/assets/styles/app.styl"></style>

<script>
	import Vue from 'vue'
	import VueHead from 'vue-head'
	Vue.use(VueHead)

    export default {
        name: 'app',
        head: {
			title: {
				inner: 'It will be a pleasure'
			},
			// Meta tags
			meta: [
				{ name: 'application-name', content: 'Ação do coração' },
				{ name: 'description', content: 'Que boa ação você faria para entrar 1 hora antes na Blackfriday mateus com R$1.000,00 no bolso? As 10 ações mais legais levarão esse prêmio.', id: 'desc' }, // id to replace intead of create element
				// ...
				// Twitter
				{ name: 'twitter:title', content: 'Ação do coração' },
				// with shorthand
				{ n: 'twitter:description', c: 'Que boa ação você faria para entrar 1 hora antes na Blackfriday mateus com R$1.000,00 no bolso? As 10 ações mais legais levarão esse prêmio.'},
				// ...
				// Google+ / Schema.org
				{ itemprop: 'name', content: 'Ação do coração' },
				{ itemprop: 'description', content: 'Que boa ação você faria para entrar 1 hora antes na Blackfriday mateus com R$1.000,00 no bolso? As 10 ações mais legais levarão esse prêmio.' },
				// ...
				// Facebook / Open Graph
				{ property: 'og:title', content: 'Ação do coração' },
				// with shorthand
				{ p: 'og:image', c: 'http://grupomateus.com.br/acaodocoracao/static/img/banner.jpg' },
				// ...
			]
		}
    }
</script>



