<template lang="pug">
    #home
        .destaque
            .container
                h2.title_campanha Que boa ação você faria para entrar 1 hora antes na Blackfriday mateus com R$1.000,00 no bolso?
                    | As 10 ações mais legais levarão esse prêmio.

                .logo_coracao Ação do coração
                .logo_mateus Mateus

        .participar
            .container
                h2.title_campanha_mobile Que boa ação você faria para entrar 1 hora antes na Blackfriday mateus com R$1.000,00 no bolso?
                    | As 10 ações mais legais levarão esse prêmio.

                .video
                    h3.title Assista o video e compartilhe.

                    .wrap_video
                        button.play(@click="playVideo", v-if="showButtonPlay")
                        youtube(@ready="ready", @ended="ended", @paused="paused", @playing="playing", class="youtube_frame", :video-id="videoId")

                .tutorial
                    h3.title É fácil participar:

                    ul.list
                        li.item
                            .number 1.
                            .text Faça uma boa ação que ajude a tornar o mundo um lugar melhor;
                        li.item
                            .number 2.
                            .text Faça um vídeo da sua ação de até 3 minutos;
                        li.item
                            .number 3.
                            .text Publique em modo público na sua rede social
                                span usando a tag
                                    strong #açãodocoraçãomateus;
                        li.item
                            .number 4.
                            .text Vamos escolher as 10 ações mais legais.

                    .more Saiba mais da promoção no regulamento,
                        a(href="#") clique aqui.
</template>

<style lang="stylus" src="@/assets/styles/home.styl"></style>

<script>
    import Vue from 'vue'
    import VueYouTubeEmbed from 'vue-youtube-embed'
    Vue.use(VueYouTubeEmbed)

    export default {
        name: 'home',
        data () {
            return {
                showButtonPlay: true,
                videoId: 'MFYJQKTH98w',
                player: null,
                msg: 'Welcome to Your Vue.js App'
            }
        },
        methods:{
            ready (player) {
                this.player = player;
            },
            ended( player ){
                this.showButtonPlay = true;
            },
            paused (player) {
                this.showButtonPlay = true;
                // console.log(player)
                // The player is playing a video.
            },
            playing (player) {
                this.showButtonPlay = false;
                // console.log(player)
                // The player is playing a video.
            },
            playVideo(){
                this.showButtonPlay = false;
                this.player.playVideo();
            }
        }
    }
</script>



